/* home css */
.main-container {
  width: 90%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 5px auto;
}
.container {
  margin: 20px auto;
  width: 230px;
  height: 100%;
  border: 5px solid #838383;
  border-radius: 10px;
}
.header img {
  max-width: 100%;
  width: 250px;
  height: 260px;
}

.title {
  font-size: 40px;
}

.container .content {
  padding: 2px;
  height: 60%;
  margin: 2px auto;
  width: 90%;
  background-color: cornsilk;
}
.content p {
  font: 15px, Roboto;
  color: #838383;
}
.content h3 {
  color: #80bf32;
}
.content button {
  height: 30px;
  background-color: firebrick;
  width: 100%;
  padding: 20px auto;
  margin: 2px auto;
  border-radius: 5px;
  border: none;
  color: white;
  cursor: pointer;
}

/* cart css */
.parent {
  display: flex;
  margin: 20px auto;
  border: 1px solid grey;
  border-radius: 14px;
}
.left {
  height: fit-content;
}
.middle {
  width: 30%;
  margin: 4px 10px;
}
.right {
  margin: 4px auto;
}
.left img {
  max-width: 130px;
  height: fit-content;
  margin: 4px;
}
.count {
  display: contents;
  margin: 0px 5px;
}

/*  External CSS */
.collection-item {
  width: 100%;
  color: black;
}
#toast-container {
  /* top: 30px !important;
  right: 20px !important;
  left: auto !important; */
  top: auto !important;
  right: auto !important;
  bottom: 10%;
  left: 7%;
}
.input-field input[type="text"]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}
.input-field input[type="password"]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}
a {
  color: black !important;
}

.brand-logo {
  font-family: "Grand Hotel", cursive;
}

.mycard {
  margin-top: 30px;
}
.auth-card {
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}
.logo {
  width: 60%;
  border: 5px solid #555;
  border-radius: 50%;
}
.homeImage {
  width: 100%;
  height: 60vh;
}
.Hover:hover {
  background-color: #f14d54 !important;
}
.categoryButton {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.categoryButton button {
  margin: 0px 10px;
  padding: 8px 15px;
  border-radius: 10px;
  border: none;
  color: white;
  background-color: firebrick !important;
  cursor: pointer;
}
@media only screen and (max-width: 430px) {
  .homeImage {
    display: none;
  }
}
@media only screen and (max-width: 625px) {
  .logo {
    display: none;
  }
}
